<template>
  <div :style="slidePosition" class=" h-100" style="width:100%;">
    <div
      class="position-absolute  text-warning gradient-mask-4"
      style="top:25%; height:50%; left:50%; width:50%;"
    >
      <div
        class="lns4m d-flex flex-nowrap align-items-end "
        style="height: 100%;width:300%;"
        ref="lns4"
      >
        <div
          v-for="(number, index) in parseInt(30 * this.aspectRatio)"
          :key="index"
          class="vertical-border border-warning"
          style="height: 100%;width:10%;border-right:solid 0.3vh;"
        ></div>
      </div>
    </div>
  </div>
</template>

<script>
import anime from "animejs"
import { slidePositionPlain } from "@/utils/slidePositioning"
export default {
  props: ["globalUnits", "slideIndex"],
  computed: {
    slidePosition() {
      //console.log("SLIDES INDEX", this.slideIndex)
      return slidePositionPlain(this.globalUnits, this.slideIndex)
    },
    innerPosition() {
      return (
        (this.globalUnits - this.slideIndex.start) /
        (this.slideIndex.end - this.slideIndex.start)
      )
    },
    aspectRatio() {
      return window.screen.availWidth / window.screen.availHeight
    }
  },

  mounted() {
    this.startLoop()
  },
  methods: {
    startLoop() {
      anime({
        loop: true,
        targets: ".lns4m",
        translateX: ["0%", "-40%"],
        direction: "alternate",
        duration: 12000,
        easing: "easeInOutSine"
      })
    },
    gradientMask4(percentage) {
      //console.log("VOY AQUI EN LUINEA 4", percentage)
      let gradientMask4 = anime
        .timeline({
          autoplay: false,
          loop: false,
          endDelay: 0
        })
        .add({
          targets: ".gradient-mask-4",

          left: ["0%", "-30%"],
          duration: 1000,
          delay: 0,
          easing: "linear"
        })

      gradientMask4.seek(
        gradientMask4.duration * percentage <= gradientMask4.duration
          ? gradientMask4.duration * percentage
          : gradientMask4.duration * 0.5
      )
    },
    lns4(percentage) {
      let lns4 = anime
        .timeline({
          autoplay: false,
          loop: false,
          endDelay: 0
        })
        .add({
          targets: ".vertical-border",

          opacity: [0, 1],
          height: (el, i) => ["0%", i * 1.25 + "%"],
          duration: 1000,
          delay: (el, i) => 4000 + 200 * i,
          easing: "easeInCubic"
        })

      lns4.seek(
        percentage <= 0.5
          ? lns4.duration * (2 * percentage)
          : lns4.duration * (1 - percentage)
      )
    },
    lns4m(percentage) {
      let lns4m = anime
        .timeline({
          autoplay: false,
          loop: false,
          endDelay: 0
        })

        .add({
          targets: ".lns4m",

          /* opacity: [100, 0], */

          translateX: ["0%", "-40%"],
          duration: 2000,
          delay: 2000,
          easing: "linear"
        })

      lns4m.seek(
        percentage <= 0.5
          ? lns4m.duration * (2 * percentage)
          : lns4m.duration * 1
      )
    }
  },
  watch: {
    innerPosition() {
      //this.lns4(this.innerPosition)
      this.lns4m(this.innerPosition)
    }
  }
}
</script>

<style>
.gradient-mask-4 {
  mask-image: linear-gradient(
    -90deg,
    transparent 0%,
    black 35%,
    black 50%,
    transparent 100%
  );
}
</style>
